import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Navbar, Nav, Container, Row, NavDropdown } from 'react-bootstrap'
import { LinkContainer } from 'react-router-bootstrap'
import SearchBox from './SearchBox'
import { logout } from '../actions/userActions'

function Header() {

    const userLogin = useSelector(state => state.userLogin)
    const { userInfo } = userLogin

    const dispatch = useDispatch()

    const logoutHandler = () =>{
        dispatch(logout())
    }

    return (
        <div >
            <header>
        
            <Navbar variant="dark" expand="lg" collapseOnSelect>
                <Container fluid >
                        <LinkContainer to='/'>
                            <Navbar.Brand>
                            <img
                                src="images/CAV.png"
                                width="50"
                                height="50"
                                className="d-inline-block"
                                alt="CAV logo"
                                />{' '}
                                CA Vitry</Navbar.Brand>
                    </LinkContainer>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        
                        <Nav
                            className="me-auto my-2 my-lg-0"
                            style={{ maxHeight: '100px' }}
                            navbarScroll
                        >

                            <LinkContainer to='/equipe'>
                                <Nav.Link>Equipe</Nav.Link>
                            </LinkContainer>

                            <LinkContainer to='/cavtv'>
                                <Nav.Link>CAV TV</Nav.Link>
                            </LinkContainer>

                            <LinkContainer to='/photo'>
                                <Nav.Link>PHOTO</Nav.Link>
                            </LinkContainer>
                            <LinkContainer to='/boutique'>
                                <Nav.Link>Boutique</Nav.Link>
                            </LinkContainer>
                            
                        </Nav>
                    
                    <Nav className="icon">
                    <SearchBox/>
                        {userInfo ? (
                            <NavDropdown title={userInfo.name} id='username'>
                                <LinkContainer to='/profile'>
                                    <NavDropdown.Item>Profile</NavDropdown.Item>
                                </LinkContainer>

                                <NavDropdown.Item onClick={logoutHandler}>Logout</NavDropdown.Item>
                            </NavDropdown>
                        ) : <LinkContainer to='/login'> 
                                <Nav.Link><i class="fa fa-user"></i>login</Nav.Link>
                            </LinkContainer>
                        }

                        

                        {userInfo && userInfo.isAdmin && (
                            <NavDropdown title='Admin' id='adminmenu'>
                            <LinkContainer to='/admin/userlist'>
                                <NavDropdown.Item>Users</NavDropdown.Item>
                            </LinkContainer>

                            <LinkContainer to='/admin/productlist'>
                                <NavDropdown.Item>Products</NavDropdown.Item>
                            </LinkContainer>

                            <LinkContainer to='/admin/orderlist'>
                                <NavDropdown.Item>Orders</NavDropdown.Item>
                            </LinkContainer>

                        </NavDropdown>
                        )}

                        <LinkContainer to='/cart'>
                            <Nav.Link><i class="fa fa-cart-plus"></i>cart</Nav.Link>
                        </LinkContainer>
                    </Nav>
                    </Navbar.Collapse>
                </Container>
                
            </Navbar>
                    
            </header>
            
        </div>
    )
}

export default Header
